span.ripple {
    position: absolute; /* The absolute position we mentioned earlier */
    border-radius: 50%;
    transform: scale(0);
    animation: ripple 600ms linear;
    background-color: rgba(167, 167, 167, 0.7);
}
@keyframes ripple {
    to {
      transform: scale(4);
      opacity: 0;
    }
}